import React from 'react';
import { Person } from 'src/models';
import PeopleRow from './PeopleRow';

interface Props {
  me: Person;
  persons: Person[];
}

export default function PeopleTable({ me, persons }: Props) {
  const rows = persons.map((person, i) => {
    const isMe = person.name === me.name && person.join.toDateString() === me.join.toDateString();
    return <PeopleRow index={i} person={person} isMe={isMe} key={person.name + person.join} />;
  });

  return (
    <div id="people-table">
      <table className="table table-striped table-hover">
        <thead>
          <tr>
            <th className="no">#</th>
            <th className="name"></th>
            <th className="rank"></th>
            <th className="date-start"></th>
            <th className="date-transfer"></th>
            <th className="date-end"></th>
            <th className="days-past"></th>
            <th className="days-left"></th>
            <th className="progressbar">{' '}</th>
          </tr>
        </thead>
        <tbody>
          {rows}
        </tbody>
      </table>
    </div>
  );
};
